.parent-container {
  background-color: rgb(60, 222, 157);/* Black */
}

.container1 {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 20px; /* Add margin bottom for spacing */
}

.output {
  border: 2px solid #8ad606; /* White */
  padding: 10px;
  width: 50%;
  background-color: rgb(56, 197, 70); /* White */
  color: #0a36c6; /* Black */
}

.welcome,
.user-info,
.user-type {
  padding: 8px;
  margin-bottom: 10px;
  color: #198e19; /* Black */
}

.user-type {
  font-weight: bold;
}

.highlight-on-hover {
  box-shadow: 0 5px 10px rgba(54, 29, 177, 0.491); /* Add box-shadow for the 3D effect */
  transition: box-shadow 0.1s ease; /* Add transition effect */
  background-color: #a41298; /* White */
  color: #000000; /* Black */
  border: none; /* Remove default button border */
  cursor: pointer; /* Add pointer cursor on hover */
  border-radius: 3px;
  padding: 14px 25px; /* Adjust padding */
  font-size: 12px; /* Adjust font size */
}

.highlight-on-hover:hover {
  box-shadow: 0 3px 10px rgba(107, 9, 164, 0.6); /* Increase shadow on hover for more depth */
}

.highlight-on-hover:active {
  background-color: #055628; /* White */
}

.student-details {
  margin-top: 20px;
  border: 2px solid #3dcd0d; /* White */
  padding: 16px;
  width: 60%;
  display: flex;
  flex-direction: row; /* Display cards side by side */
  flex-wrap: wrap; /* Allow cards to wrap to the next line if necessary */
  justify-content: center;
  background-color: #066c21; /* White */
  color: #71438e; /* Black */
}

.student-card {
  border: 1px solid #0a0101a5;
  padding: 10px; /* Reduced padding for a smaller size */
  margin: 10px; /* Add margin between cards */
  background-color: rgb(111, 174, 22); /* White */
  width: calc(40% - 20px); /* Set width of each card, subtracting margin */
  max-width: 200px; /* Limit max width of the cards */
  box-shadow: 0 4px 8px rgba(35, 2, 2, 0.493);
  transition: transform 0.3s ease-in-out;
  border-radius: 42px; /* Smooth transition for box-shadow */
}



.student-card h3 {
  margin-top: 5px; /* Add missing unit */
  font-size: 16px; /* Reduced font size of student name */
}

.student-card p {
  margin: 5px 0; /* Reduced paragraph margin */
}

.card-title {
  font-size: 18px;
  margin-bottom: 10px;
}

.cont2 {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
}